import React, { Component } from 'react';
import { LoggedIn, LoggedOut, AuthButton, Value, withWebId } from '@solid/react';
import './App.css';
import auth from 'solid-auth-client'

const CrossStorageHub = require('cross-storage').CrossStorageHub

let DEV_URL = '';
if (process.env.NODE_ENV === 'development') {
  DEV_URL = 'http://localhost:3000';
 }

class App extends Component {
  constructor(props) {
   super(props);
}

componentDidMount () {
  CrossStorageHub.init([
    {origin: /books.google.com/, allow: ['get', 'set', 'del', 'getKeys', 'clear']}
  ]);
} 

saveState () {
  auth.trackSession(session => {
    if (!session)
      return <div></div>
    else{
      localStorage.setItem('loginValue',JSON.stringify(true));
      localStorage.setItem('session',JSON.stringify(session));
      return <div></div>
    }
  });
}

  render() {
   return (
    <div className="Login-wrapper">
    <div className="Login-formWrapper">
      <h1 id="Login-title">Connection</h1>
      <LoggedIn>
        <p id="Login-Label1">You are logged in as '<Value src="user.name"/>'.</p>
        { this.saveState() } 
      </LoggedIn>
      <LoggedOut>
        <p id="Login-Label2">Please log in to your pod.</p>
        <p id="Login-authButton"><AuthButton popup="popup.html"/></p>
        <p id="Login-registerButton">
          You don't have an account ? 
          <a href="https://solid.github.io/solid-idps/" target="__blank">Register here.</a>
        </p>
      </LoggedOut>
    </div>
  </div>
   );
  }
 }
 export default App;